.menu {
    // on top
    position: relative;
    z-index: 10;

    display: flex;
    align-items: center;
    justify-content: center;

    margin-left: 3vw;
    margin-right: 3vw;
    // background-image: linear-gradient(white, rgb(187, 187, 187), white);
    background-color: rgb(182, 232, 255);
    width: 80%;
    height: 20%;

    box-shadow: 0px 0px 3px 3px rgb(129, 215, 255);
    border-color: black;
    border-radius: 10px;
    border-style: 10px outset;

    h4 {
        margin-inline: 1vw;
        // margin: auto;
    }

    ol {
        margin-left: 5%;
        list-style-type: none;
        padding: 0;
        margin: 0;
        font-size: 18px;
        height: 2em;
        line-height: 2em;
        text-align: center;

        a {
            // whole menu
            text-decoration: none;
            color: rgb(0, 0, 0);
        }
    }

    ol>li {
        //menu header
        float: left;
        // background-color: #fdd700;
        background-image: linear-gradient(white, rgb(0, 195, 255));
        width: 150px;
        margin-left: 2px;
        border-radius: 10px;
        border: 5px outset rgb(155, 155, 155);
        box-shadow: 5px 5px 5px 0px grey;
    }

    ol>li:hover {
        //menu header
        // background-color: rgb(158, 190, 255);
        // background-image: linear-gradient(white, rgb(43, 172, 174), white);
        background-image: linear-gradient(rgb(255, 255, 255), rgb(0, 217, 255));
    }

    ol>li:hover>a {
        //menu header, hover, whole field
        color: rgb(0, 0, 0);
        font-weight: bold;
    }

    ol>li>a:hover {
        //     //menu header, hover, link only
        color: rgb(0, 38, 255);
    }

    ol>li>ul {
        list-style-type: none;
        padding: 0;
        margin: 0;
    }

    ol>li:first-child {
        margin-left: 0;
    }

    // ol > li > ul > li > a {
    ol>li>ul>li {
        //lists
        border: 5px outset rgb(155, 155, 155);
        // border-top-width: 100%;
        //----
        border-radius: 10px;
        background-image: linear-gradient(white, rgb(184, 184, 184), white);
        // background-color: rgb(221, 255, 0);
        box-shadow: 5px 5px 5px 0px grey;
    }

    ol>li>ul>li:hover a {
        //lists - font color
        color: rgb(0, 0, 0);
        font-weight: bold;
    }

    ol>li>ul>li:hover {
        //lists
        background-image: linear-gradient(white, rgb(52, 181, 255), white);
        // background-color: rgb(154, 231, 255);
    }

    ol>li>ul>li:active {
        background-image: radial-gradient(white 50%, rgb(251, 255, 1));
        transform: scale(1.3);
    }

    ol>li>ul {
        display: none;
    }

    ol>li:hover>ul {
        display: block;
    }

    // keep on place
    ol>li {
        float: left;
        background-color: #fdd700;
        width: 150px;
        margin-left: 1px;
        height: 2em;
    }
}