.menu {
  position: relative;
  z-index: 10;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 3vw;
  margin-right: 3vw;
  background-color: rgb(182, 232, 255);
  width: 80%;
  height: 20%;
  box-shadow: 0px 0px 3px 3px rgb(129, 215, 255);
  border-color: black;
  border-radius: 10px;
  border-style: 10px outset;
}
.menu h4 {
  margin-inline: 1vw;
}
.menu ol {
  margin-left: 5%;
  list-style-type: none;
  padding: 0;
  margin: 0;
  font-size: 18px;
  height: 2em;
  line-height: 2em;
  text-align: center;
}
.menu ol a {
  text-decoration: none;
  color: rgb(0, 0, 0);
}
.menu ol > li {
  float: left;
  background-image: linear-gradient(white, rgb(0, 195, 255));
  width: 150px;
  margin-left: 2px;
  border-radius: 10px;
  border: 5px outset rgb(155, 155, 155);
  box-shadow: 5px 5px 5px 0px grey;
}
.menu ol > li:hover {
  background-image: linear-gradient(rgb(255, 255, 255), rgb(0, 217, 255));
}
.menu ol > li:hover > a {
  color: rgb(0, 0, 0);
  font-weight: bold;
}
.menu ol > li > a:hover {
  color: rgb(0, 38, 255);
}
.menu ol > li > ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
}
.menu ol > li:first-child {
  margin-left: 0;
}
.menu ol > li > ul > li {
  border: 5px outset rgb(155, 155, 155);
  border-radius: 10px;
  background-image: linear-gradient(white, rgb(184, 184, 184), white);
  box-shadow: 5px 5px 5px 0px grey;
}
.menu ol > li > ul > li:hover a {
  color: rgb(0, 0, 0);
  font-weight: bold;
}
.menu ol > li > ul > li:hover {
  background-image: linear-gradient(white, rgb(52, 181, 255), white);
}
.menu ol > li > ul > li:active {
  background-image: radial-gradient(white 50%, rgb(251, 255, 1));
  transform: scale(1.3);
}
.menu ol > li > ul {
  display: none;
}
.menu ol > li:hover > ul {
  display: block;
}
.menu ol > li {
  float: left;
  background-color: #fdd700;
  width: 150px;
  margin-left: 1px;
  height: 2em;
}